*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}
h2{
  font-size: 6rem;
}
.weather{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0,0.4);
  color: white;
}
.weather::before{
  content: '';
  background: url('./asset/img1.jpg')no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left:0;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.weather .top {
  width: 100%;
  margin: 1rem auto;
}

.weather .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.weather .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255, 255, 0.2);
}

.bold{
  font-weight: 700;
}
.weather .search{
  text-align: center;
  padding: 1rem;
}
.weather input{
  /* padding: .7rem 1,5rem; */
  padding: 5px 20px;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255,255, 0.5);
  background: rgba(255,255,255,0.1);
}
::placeholder{
  color: white;
}